import React from 'react';
import PropTypes from 'prop-types';
import './Movie.css';

function Movie({ id, title, summary, poster, year, rating, genres }) {
  return (
    <div className="movie">
      <img className="movie_poster" src={poster} alt={title} title={title} />
      <h3 className="movie_title">
        {title}({year})
      </h3>
      {/* <h6 className="movie_year">{year}</h6> */}
      <ul className="movie_genres">
        {genres.map((genre, index) => {
          return (
            <li className="movie_genre" key={index}>
              {genre}
            </li>
          );
        })}
      </ul>
      <h6 className="movie_rating">
        {rating} /10.0 ,{' '}
        {rating >= 9.0 ? 'Excellent!' : rating >= 8.5 ? 'Good!' : 'So So...'}
      </h6>
      <h6 className="movie_summary">{summary}</h6>
    </div>
  );
}
Movie.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired,
  year: PropTypes.number.isRequired,
  rating: PropTypes.number.isRequired,
  genres: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default Movie;
